import React, { useState, createRef, useEffect } from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import ScaledImage from '../ScaledImage'
import useScrollPosition from '../../hooks/useScrollPosition'
import { getDimensions, getLinkPath } from '../../utils'
import Button from '../Button'

const StyledButton = styled(Button)`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  height: ${mobileVw(40)};
  padding: 0 ${mobileVw(16)};
  min-width: ${mobileVw(90)};
  span {
    font-weight: normal;
    font-size: ${mobileVw(14)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(15)};
    height: ${desktopVw(40)};
    min-width: ${desktopVw(90)};
    margin-right: ${desktopVw(25)};
    span {
      font-size: ${desktopVw(14)};
    }
  }
`

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
  }
`
const StyledImagWrapper = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: sticky;
    top: ${props => desktopVw(props.theme.header.desktop.height)};
    height: ${desktopVw(900)};
    width: 50vw;
  }
`

const StyledScaledImage = styled(ScaledImage)`
  width: ${props => (props.singleImage ? '100%' : mobileVw(271))};
  height: ${props => (props.singleImage ? mobileVw(392) : mobileVw(339))};
  display: inline-block;
  margin-right: ${props => (props.singleImage ? 0 : mobileVw(11))};
  &:first-of-type {
    margin-left: ${props => (props.singleImage ? 0 : mobileVw(10))};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(401)};
    width: ${desktopVw(321)};
    margin-right: 0;
    margin-bottom: ${desktopVw(32)};
    display: block;
    &:first-of-type {
      margin-left: 0;
    }
  }
`

const LeftWrapper = styled.div`
  flex: 1;
`

const RightWrapper = styled.div`
  flex: 1;
  position: relative;
`

const CardContainer = styled.div`
  position: relative;
  height: ${desktopVw(900)};
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: ${desktopVw(75)} ${desktopVw(200)};
  padding-right: 0;
  box-sizing: border-box;
`

const CardHeading = styled.div`
  font-family: ${props => props.theme.fonts.secondary};
  font-size: ${mobileVw(34)};
  line-height: ${mobileVw(34)};
  margin-bottom: ${mobileVw(26)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    max-width: ${desktopVw(340)};
    font-size: ${desktopVw(48)};
    line-height: ${desktopVw(48)};
    margin-bottom: ${desktopVw(25)};
  }
`

const TextBlock = styled.div`
  font-size: ${mobileVw(14)};
  line-height: ${mobileVw(20)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(14)};
    line-height: ${desktopVw(20)};
    padding-right: ${desktopVw(20)};
    width: ${desktopVw(301)};
  }
`
const ButtonContainer = styled.div`
  display: flex;
  width: 100%;
  margin-top: ${mobileVw(28)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(28)};
  }
`

const MobileImageWrapper = styled.div`
  overflow-x: scroll;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar {
    display: none;
  }
`

const MobileCardContainer = styled.div`
  padding: ${mobileVw(58)} ${mobileVw(30)} 0;
  text-align: center;
`

const ScrollableSticky = ({
  cards,
  backgroundColor,
  productsImageOverride,
  metaTags,
}) => {
  const [activeIndex, setactiveIndex] = useState(0)
  const [cardsRef, setCardsRef] = useState({})

  useScrollPosition(() => {
    const offset = 0
    const scrollPosition = window.scrollY + offset

    const selected = Object.keys(cardsRef).find(card => {
      const ele = cardsRef[card].current
      if (ele) {
        const { offsetBottom, offsetTop } = getDimensions(ele)
        return scrollPosition > offsetTop && scrollPosition < offsetBottom
      }
    })
    if (selected) setactiveIndex(selected)
  })

  useEffect(() => {
    const cardsRefs = cards.reduce((acc, currentVal, index) => {
      return {
        ...acc,
        [index]: createRef(),
      }
    }, {})
    setCardsRef(cardsRefs)
  }, [])

  const { devices } = useDetectDevices()
  if (devices.desktop) {
    return (
      <StyledWrapper bgColor={backgroundColor?.hexcode}>
        <LeftWrapper>
          <StyledImagWrapper>
            {cards.map((it, index) => (
              <ScaledImage
                fluid={it?.imageDesktop?.fluid}
                style={{
                  height: desktopVw(900),
                  position: 'absolute',
                  opacity: index == activeIndex ? '1' : '0',
                }}
              />
            ))}
          </StyledImagWrapper>
        </LeftWrapper>
        <RightWrapper>
          {cards.map((it, index) => (
            <CardContainer ref={cardsRef[index]}>
              <StyledScaledImage fluid={it?.image?.fluid} />
              <CardHeading>{it?.title?.title}</CardHeading>
              <TextBlock>{documentToReactComponents(it?.text?.json)}</TextBlock>
              <ButtonContainer>
                {(it?.buttons ?? []).map(btn => (
                  <StyledButton to={getLinkPath(btn)}>{btn.text}</StyledButton>
                ))}
              </ButtonContainer>
            </CardContainer>
          ))}
        </RightWrapper>
      </StyledWrapper>
    )
  }
  return null
}

export default ScrollableSticky
