import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'

const SVGDesktop = styled.img`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block !important;
    margin: 0 ${desktopVw(215)};
  }
`
const SVGImageMobile = styled.img`
  display: block;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`
const Wrapper = styled.div`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  text-align: center;
  padding-top: ${mobileVw(56)};
  padding-bottom: ${mobileVw(30)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-top: ${desktopVw(140)};
    padding-bottom: ${desktopVw(112)};
  }
`

const PageHeader = styled.div`
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${mobileVw(34)};
    line-height: ${mobileVw(34)};
    font-weight: 400;
    white-space: normal;
    margin: 0 ${mobileVw(50)};
    &:last-of-type {
      margin-bottom: ${mobileVw(31)};
    }
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(69)};
      line-height: ${desktopVw(72)};
      margin: 0;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
`
const StyledTextWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    min-height: ${desktopVw(222)};
    flex-direction: row;
  }
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(18)};
    margin: 0 ${mobileVw(50)} ${mobileVw(25)};
    padding: 0 ${mobileVw(30)};
    &:last-of-type {
      margin-bottom: ${mobileVw(57)};
    }
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
      margin: 0;
      margin-right: ${desktopVw(85)};
      width: ${desktopVw(270)};
      padding: 0;
      box-sizing: border-box;
      &:not(:last-of-type, :first-of-type) {
        padding: 0 ${desktopVw(20)};
      }
      &:last-of-type {
        margin-bottom: 0;
        margin-right: 0;
      }
    }
  }
`

const HowItWorks = ({
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  backgroundColor,
  textDesktop,
  textMobile,
}) => {
  const { devices } = useDetectDevices()
  return (
    <Wrapper bgColor={backgroundColor?.hexcode}>
      <PageHeader>
        {devices.desktop
          ? documentToReactComponents(headingDesktop?.json)
          : documentToReactComponents(headingMobile?.json)}
      </PageHeader>
      <StyledTextWrapper>
        {devices.desktop
          ? documentToReactComponents(textDesktop?.json)
          : documentToReactComponents(textMobile?.json)}
      </StyledTextWrapper>
      <SVGDesktop src={imageDesktop?.file?.url} />
      <SVGImageMobile src={imageMobile?.file?.url} />
    </Wrapper>
  )
}

export default HowItWorks
