import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import React from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import ScaledImage from '../ScaledImage'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  position: relative;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-direction: ${props =>
      props.desktopLayout === 'Content Left' ? 'row-reverse' : 'row'};
    margin: ${desktopVw(120)} 0;
  }
`

const StyledScaledImage = styled(ScaledImage)`
  width: ${props => (props.singleImage ? '100%' : '271px')};
  height: ${props => (props.singleImage ? '392px' : '339px')};
  display: inline-block;
  /* margin-right: ${props => (props.singleImage ? 0 : mobileVw(11))}; */
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(660)};
    width: ${desktopVw(1000)};
  }
`

const TextCard = styled.div`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: absolute;
    width: ${desktopVw(720)};
    height: ${desktopVw(500)};
    top: 50%;
    transform: translateY(-50%);
    left: ${props => (props.desktopLayout === 'Content Left' ? '0' : 'unset')};
    right: ${props => (props.desktopLayout !== 'Content Left' ? '0' : 'unset')};
    padding: 0 ${desktopVw(128)} 0 ${desktopVw(188)};
    box-sizing: border-box;
    display: flex;
    align-items: center;
    h1 {
      font-size: ${desktopVw(48)};
      line-height: ${desktopVw(48)};
      font-family: ${props => props.theme.fonts.secondary};
      &:first-of-type {
        margin-top: 0;
      }
      margin-top: ${desktopVw(50)};
      margin-bottom: ${props =>
        props.hasIcon ? desktopVw(13) : desktopVw(41)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      width: ${desktopVw(390)};
    }
  }
`

const SVGDesktop = styled.img`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block !important;
    z-index: 1;
  }
`

const TextBlock = styled.div``

const ImageOverlappedWithCard = ({
  imageDesktop,
  textDesktop,
  metaTags,
  textSectionDesktopLayout,
  backgroundColor,
  productImageOverride,
}) => {
  const { devices } = useDetectDevices()

  if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && devices.desktop) {
    return (
      <StyledWrapper desktopLayout={textSectionDesktopLayout}>
        <StyledScaledImage fluid={imageDesktop?.fluid} />
        <TextCard
          bgColor={backgroundColor?.hexcode}
          desktopLayout={textSectionDesktopLayout}
          hasIcon={productImageOverride}
        >
          <TextBlock>
            {documentToReactComponents(textDesktop?.json)}
            {productImageOverride &&
              (productImageOverride?.fluid ? (
                <Image fluid={productImageOverride?.fluid} />
              ) : (
                <SVGDesktop src={productImageOverride?.file?.url} />
              ))}
          </TextBlock>
        </TextCard>
      </StyledWrapper>
    )
  }
  return null
}

export default ImageOverlappedWithCard
