import React from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  text-align: center;
  padding: ${props =>
    props.iconAtBottom
      ? `${mobileVw(59)} ${mobileVw(28)} ${mobileVw(44)}`
      : `${mobileVw(68)} ${mobileVw(16)} ${mobileVw(70)}`};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(112)} ${desktopVw(160)} ${desktopVw(81)}
      ${desktopVw(160)};
  }
`

const PageHeader = styled.div`
  text-align: center;
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    /* margin-bottom: ${mobileVw(26)}; */
    font-size: ${props =>
      props.headingSizeMobile === 'Large' ? mobileVw(48) : mobileVw(28)};
    line-height: ${props =>
      props.headingSizeMobile === 'Large' ? mobileVw(72) : mobileVw(30)};
    font-weight: 400;
    /* white-space: nowrap; */
    text-align: center;
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(60)};
      margin-bottom: ${desktopVw(35)};
      line-height: ${desktopVw(60)};
    }
  }
`

const Text = styled.div`
  margin-top: ${props => props.marginTop ?? mobileVw(59)};
  h4 {
    font-family: ${props => props.theme.fonts.primary};
    font-size: ${mobileVw(20)};
    line-height: ${mobileVw(25)};
  }
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
    &:first-of-type {
      padding-top: ${mobileVw(5)};
    }
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: 0;
    h4 {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      padding: 0 ${desktopVw(82)};
      &:first-of-type {
        padding-top: ${desktopVw(35)};
      }
    }
  }
`

const SVGDesktop = styled.img`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block !important;
    text-align: center;
    margin: ${desktopVw(25)} auto 0;
  }
`
const SVGImageMobile = styled.img`
  display: block;
  margin: ${mobileVw(9)} auto 0;
  /* width: 100%; */
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const BannerWithCenterTextAndIcon = ({
  headingDesktop,
  headingMobile,
  textDesktop,
  textMobile,
  imageMobile,
  imageDesktop,
  backgroundColor,
  metaTags,
  textSectionMobileLayout,
  headingSizeMobile,
}) => {
  const { devices } = useDetectDevices()
  if (metaTags) {
    if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && !devices.desktop)
      return null
    if (metaTags.includes('SHOW_ONLY_ON_MOBILE') && !devices.mobile) return null
  }
  return (
    <StyledWrapper
      bgColor={backgroundColor?.hexcode}
      iconAtBottom={textSectionMobileLayout === 'Content Top'}
    >
      <PageHeader headingSizeMobile={headingSizeMobile}>
        {devices.desktop
          ? documentToReactComponents(headingDesktop?.json)
          : documentToReactComponents(headingMobile?.json)}
      </PageHeader>
      {textSectionMobileLayout === 'Content Top' ? (
        <>
          <Text marginTop={0}>
            {devices.desktop
              ? documentToReactComponents(textDesktop?.json)
              : documentToReactComponents(textMobile?.json)}
          </Text>
          {imageMobile?.fluid ? (
            <Image fluid={imageMobile?.fluid} />
          ) : (
            <SVGImageMobile src={imageMobile?.file?.url} />
          )}
        </>
      ) : (
        <>
          <SVGImageMobile src={imageMobile?.file?.url} />
          <Text>
            {devices.desktop
              ? documentToReactComponents(textDesktop?.json)
              : documentToReactComponents(textMobile?.json)}
          </Text>
        </>
      )}
      <SVGDesktop src={imageDesktop?.file?.url} />
    </StyledWrapper>
  )
}

export default BannerWithCenterTextAndIcon
