import React from 'react'
import styled from 'styled-components'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import ScaledImage from '../ScaledImage'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  margin-top: -${mobileVw(21)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
  }
`

const StyledScaledImage = styled(ScaledImage)`
  width: ${mobileVw(354)};
  height: ${mobileVw(374)};
  margin: ${mobileVw(10)};
`
const StyledScaledImageTopLayer = styled(ScaledImage)`
  width: ${mobileVw(284)};
  height: ${mobileVw(333)};
  margin: auto;
  margin-bottom: -50%;
  z-index: 1;
  position: relative;
`

const MobileCardContainer = styled.div`
  /* padding: ${mobileVw(58)} ${mobileVw(30)} 0; */
  text-align: center;
`

const ImageCardWithOverlappedImage = ({
  backgroundColor,
  productImageOverride,
  metaTags,
  imageMobile,
}) => {
  const { devices } = useDetectDevices()
  if (metaTags) {
    if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && !devices.desktop)
      return null
    if (metaTags.includes('SHOW_ONLY_ON_MOBILE') && !devices.mobile) return null
  }

  return (
    <StyledWrapper bgColor={backgroundColor?.hexcode}>
      <MobileCardContainer>
        <StyledScaledImageTopLayer fluid={productImageOverride?.fluid} />
        <StyledScaledImage singleImage fluid={imageMobile?.fluid} />
      </MobileCardContainer>
    </StyledWrapper>
  )
}

export default ImageCardWithOverlappedImage
