import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import Video from '../Video'

import { desktopVw, mobileVw } from '../../styles/utils'
import useMounted from '../../hooks/useMounted'
import theme from '../../styles/theme'

const VideoWrapper = styled.div`
  position: relative;
`
const PageHeader = styled.div`
  text-align: center;
  position: absolute;
  left: 50%;
  z-index: 1;
  top: ${props =>
    props.desktopLayout === 'Content Center' ? mobileVw(49) : mobileVw(55)};
  transform: translateX(-50%);
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${props =>
      props.desktopLayout === 'Content Center'
        ? desktopVw(30)
        : desktopVw(100)};
    transform: translateX(-50%);
  }
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${mobileVw(52)};
    line-height: ${mobileVw(60)};
    font-weight: 400;
    white-space: nowrap;
    margin-bottom: ${props =>
      props.desktopLayout === 'Content Left' ? mobileVw(2) : mobileVw(13)};
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${props =>
        props.desktopLayout === 'Content Left'
          ? desktopVw(85)
          : desktopVw(102)};
      line-height: ${props =>
        props.desktopLayout === 'Content Left' ? desktopVw(50) : desktopVw(60)};
      margin-bottom: ${props =>
        props.desktopLayout === 'Content Left' ? desktopVw(12) : desktopVw(36)};
    }
  }
  h2 {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${mobileVw(54)};
    line-height: ${mobileVw(50)};
    font-weight: 400;
    white-space: nowrap;
    margin-bottom: ${mobileVw(2)};
    & + p {
      font-size: ${mobileVw(12)};
      line-height: ${mobileVw(14)};
      @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
        font-size: ${desktopVw(14)};
        line-height: ${desktopVw(25)};
      }
    }
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(68)};
      line-height: ${desktopVw(50)};
      margin-bottom: 0;
    }
  }
  p {
    white-space: nowrap;
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
    b {
      font-weight: 500;
    }
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
    }
  }
`

const StyledVideo = styled(Video)`
  height: ${mobileVw(563)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${props => `calc(100vh - ${props.height}px)`}; //TODO change
  }
`

const BackgroundVideoWithText = ({
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  textSectionDesktopLayout,
  textSectionMobileLayout,
}) => {
  const { devices } = useDetectDevices()
  const { mounted } = useMounted()
  const offsetHeight = mounted
    ? (parseFloat(desktopVw(theme.header.desktop.height)) * window.innerWidth) /
        100 +
      (parseFloat(desktopVw(theme.announcementBar.desktop.height)) *
        window.innerWidth) /
        100
    : '150'
  return (
    <VideoWrapper>
      <PageHeader desktopLayout={textSectionDesktopLayout}>
        {devices.desktop
          ? documentToReactComponents(headingDesktop?.json)
          : documentToReactComponents(headingMobile?.json)}
      </PageHeader>
      <StyledVideo
        url={devices.desktop ? imageDesktop?.file?.url : imageMobile?.file?.url}
        lazyLoad={false}
        height={offsetHeight}
        preload='auto'
      />
    </VideoWrapper>
  )
}

export default BackgroundVideoWithText
