import React from 'react'

import { desktopVw } from '../../styles/utils'

// Styles
import styled from 'styled-components'

import Button from '../Button'
import LandingRating from '../LandingRating'
import RichText from '../RichText'

const StyledRichText = styled(RichText)`
  padding: 0 12px;
  h1,
  h2,
  h3,
  h4,
  p {
    text-align: center;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(90)};
    p {
      font-size: ${desktopVw(16)};
      line-height: ${desktopVw(25)};
      margin-bottom: ${desktopVw(8)};
    }
  }
`

const ReviewContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
`

const ReviewText = styled.p`
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(20)};
    line-height: ${desktopVw(25)};
  }
`

const StyledProductRating = styled(LandingRating)`
  margin: 28px 0 12px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(32)} 0;
  }
`

const StyledReview = styled.div`
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: ${desktopVw(30)};
  }

  .rating__star {
    width: 29px;
    height: 29px;
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      width: ${desktopVw(33)};
      height: ${desktopVw(32)};
    }
  }
`

const ReviewsSection = ({ firstReview, secondReview }) => {
  let reviewList = [firstReview, secondReview].filter(r => r !== undefined)
  return (
    <>
      <StyledReview>
        {reviewList.map(review => (
          <ReviewContainer>
            <StyledProductRating color='#F0B44F' />
            <StyledRichText json={review.json} />
          </ReviewContainer>
        ))}
      </StyledReview>
    </>
  )
}

export default ReviewsSection
