import React from 'react'
import styled from 'styled-components'
import { imageSources } from '../../utils'
import ScaledImage from '../ScaledImage'
import { desktopVw, mobileVw } from '../../styles/utils'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'

const StyledSection = styled.section``

const StyledImage = styled(ScaledImage)`
  width: 100%;
  margin-top: -60px;
  height: ${mobileVw(553)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: -${desktopVw(80)};
    height: ${desktopVw(818)};
  }
`

const CenteredCardLayout = styled.div`
  box-sizing: border-box;
  background-color: white;
  text-align: center;
  margin: 0 21px;
  padding: 38px 21px 21px;
  left: 0;
  right: 0;

  p {
    font-size: 20px;
    line-height: 25px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: absolute;
    left: 0;
    right: 0;
    top: ${desktopVw(541)};
    width: ${desktopVw(801)};
    height: ${desktopVw(328)};
    padding: ${desktopVw(98)} ${desktopVw(130)} ${desktopVw(0)};
    margin: auto;

    p {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
    }
  }
`

const Title = styled.div`
  font-family: ${props => props.theme.fonts.secondary};
  text-align: center;
  font-size: ${props => props.fontSize || '80px'};
  line-height: 75px;
  margin-bottom: 23px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${props => desktopVw(props.fontSizeDesktop || 80)};
    line-height: ${desktopVw(75)};
    margin-bottom: ${desktopVw(23)};
  }
`

function HeroWithCardSection({ card, imageDesktop, imageMobile }) {
  const { devices } = useDetectDevices()

  return (
    <StyledSection>
      <StyledImage
        fluid={imageSources(imageMobile, imageDesktop)}
        style={{
          width: '100%',
        }}
      />
      <CenteredCardLayout>
        {devices.desktop && (
          <Title
            fontSize={card?.titleFontSize}
            fontSizeDesktop={card?.titleFontSizeDesktop}
          >
            {card?.title?.title}
          </Title>
        )}
        {documentToReactComponents(card?.text?.json)}
      </CenteredCardLayout>
    </StyledSection>
  )
}

export default HeroWithCardSection
