import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import React from 'react'
import styled from 'styled-components'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import ScaledImage from '../ScaledImage'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  padding: 0;
  display: flex;
  align-items: center;
  overflow: hidden;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    justify-content: space-between;
    margin: ${desktopVw(120)} 0;
  }
`

const StyledScaledImage = styled(ScaledImage)`
  width: ${mobileVw(355)};
  /* height: ${mobileVw(374)}; */
  display: inline-block;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(355)};
    height: ${desktopVw(748)};
    display: block;
  }
`

const TextCard = styled.div`
  position: absolute;
  z-index: 2;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  bottom: ${props =>
    props.mobileTextLayout === 'Content Bottom' ? mobileVw(44) : 'unset'};
  h1 {
    font-size: ${mobileVw(48)};
    line-height: ${mobileVw(48)};
    font-family: ${props => props.theme.fonts.secondary};
    font-weight: 400;
    margin-bottom: ${mobileVw(24)};
  }
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
    width: ${mobileVw(290)};
  }
`

const Card = styled.div`
  position: relative;
  padding: ${mobileVw(10)};
`

const ImageCardWithAlignText = ({
  imageMobile,
  metaTags,
  textMobile,
  textSectionMobileLayout,
}) => {
  const { devices } = useDetectDevices()
  if (metaTags) {
    if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && !devices.desktop)
      return null
    if (metaTags.includes('SHOW_ONLY_ON_MOBILE') && !devices.mobile) return null
  }

  return (
    <StyledWrapper>
      <Card>
        <StyledScaledImage fluid={imageMobile?.fluid} />
        <TextCard mobileTextLayout={textSectionMobileLayout}>
          {documentToReactComponents(textMobile?.json)}
        </TextCard>
      </Card>
    </StyledWrapper>
  )
}

export default ImageCardWithAlignText
