import React from 'react'
import BannerLowerOverlapped from './BannerLowerOverlapped'
import BannerUpperOverlapped from './BannerUpperOverlapped'

const BannerOverlapped = ({ bannerOverlappedLayout, ...props }) => {
  if (bannerOverlappedLayout == 'Top Overlapped') {
    return <BannerUpperOverlapped {...props} />
  }
  return <BannerLowerOverlapped {...props} />
}

export default BannerOverlapped
