import React from 'react'
import styled from 'styled-components'
import { desktopVw, mobileVw } from '../../styles/utils'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { ProductsList } from './common/ProductsList'
import { ImageWithLink } from './common/ImageWithLink'

const StyledSection = styled.section``

const StyledImageWithLink = styled(ImageWithLink)`
  width: unset;
  height: ${mobileVw(445)};
  padding: 23px 11px 0;
  margin-bottom: 23px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(720)};
    height: ${desktopVw(840)};
    margin: 0 0 0 ${desktopVw(640)};
    padding: 0;
  }
`

const Container = styled.div`
  margin-bottom: 54px;
  margin-top: ${mobileVw(-90)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(-50)};
    margin-bottom: ${desktopVw(-380)};
  }
`

const TextSection = styled.div`
  position: relative;
  z-index: 1;
  top: ${mobileVw(84)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${desktopVw(54)};
  }
`

const Title = styled.div`
  text-align: center;
  padding: 0 79px;
  margin-bottom: 20px;
  p {
    font-family: ${props => props.theme.fonts.secondary};
    font-weight: 500;
    font-size: 85px;
    line-height: 76px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(20)};
    padding: 0;
    p {
      font-size: ${desktopVw(85)};
      line-height: ${desktopVw(76)};
    }
  }
`

const TextDescription = styled.div`
  padding: 0 11px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    bottom: ${desktopVw(586)};
    left: ${desktopVw(760)};
    padding: 0;
    max-width: ${desktopVw(440)};
    p {
      line-height: ${desktopVw(26)};
    }
  }
`

const SecondImageWithLink = styled(StyledImageWithLink)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    bottom: ${desktopVw(380)};
    width: ${desktopVw(560)};
    height: ${desktopVw(648)};
    margin-left: ${desktopVw(120)};
  }
`

const ImagesOverlappedWithProducts = ({
  imageDesktop,
  imageDesktopLink,
  imageMobile,
  imageMobileLink,
  textDesktop,
  secondImageDesktop,
  secondImageDesktopLink,
  textMobile,
  headingDesktop,
  headingMobile,
  products,
  productsColor,
  sectionType,
  productsImageOverride,
}) => {
  const { devices } = useDetectDevices()

  return (
    <StyledSection>
      <Container>
        <TextSection>
          {devices.desktop && (
            <Title>{documentToReactComponents(headingDesktop?.json)}</Title>
          )}
          {devices.mobile && (
            <Title>{documentToReactComponents(headingMobile?.json)}</Title>
          )}
        </TextSection>
        <StyledImageWithLink
          imageDesktopLink={imageDesktopLink}
          imageMobile={imageMobile}
          imageDesktop={imageDesktop}
          imageMobileLink={imageMobileLink}
          tracker='landing_page__images_overlapped_with_products__image_1'
        />
        {devices.desktop && (
          <SecondImageWithLink
            imageDesktopLink={secondImageDesktopLink}
            imageDesktop={secondImageDesktop}
            tracker='landing_page__images_overlapped_with_products__image_2'
          />
        )}
        {devices.desktop && (
          <TextDescription>
            {documentToReactComponents(textDesktop?.json)}
          </TextDescription>
        )}
        {devices.mobile && (
          <TextDescription>
            {documentToReactComponents(textMobile?.json)}
          </TextDescription>
        )}
      </Container>
      <ProductsList
        products={products}
        productsColor={productsColor}
        productsImageOverride={productsImageOverride}
        addToCartTrackerSection={sectionType}
        tracker='landing_page__images_overlapped_with_products'
      />
    </StyledSection>
  )
}

export default ImagesOverlappedWithProducts
