import React from 'react'
import styled from 'styled-components'
import { desktopVw, mobileVw } from '../../styles/utils'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { ProductsList } from './common/ProductsList'
import PairWithThumbnail from '../PairWithThumbnail'
import { ImageWithLink } from './common/ImageWithLink'

const StyledSection = styled.section``

const StyledImageWithLink = styled(ImageWithLink)`
  width: unset;
  height: ${mobileVw(497)};
  padding: 35px 0 56px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(720)};
    height: ${desktopVw(960)};
    padding: 0;
  }
`

const Container = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 66px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin-top: ${desktopVw(74)};
    padding-bottom: ${desktopVw(80)};
  }
`

const LeftContainer = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-top: ${desktopVw(80)};
    margin-right: ${desktopVw(120)};
  }
`

const TextSection = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
  }
`

const Title = styled.div`
  text-align: center;
  padding: 0 79px;
  margin-bottom: 20px;
  p {
    font-family: ${props => props.theme.fonts.secondary};
    font-weight: 500;
    font-size: 85px;
    line-height: 76px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(20)};
    padding: 0;
    p {
      font-size: ${desktopVw(85)};
      line-height: ${desktopVw(76)};
    }
  }
`

const TextDescription = styled.div`
  padding: 0 21px;
  text-align: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    max-width: ${desktopVw(440)};
    p {
      line-height: ${desktopVw(26)};
    }
  }
`

const ProductItem = styled.div`
  .pair__with__thumbnail_image_wrapper {
    width: 100%;
    height: ${mobileVw(416)};
  }

  padding: 0 21px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: unset;
    width: ${desktopVw(480)};

    .pair__with__thumbnail_image_wrapper {
      width: ${desktopVw(480)};
      height: ${desktopVw(592)};
    }
  }
`

const FeaturedProductImageWithProducts = ({
  imageDesktop,
  imageDesktopLink,
  imageMobile,
  imageMobileLink,
  textDesktop,
  textMobile,
  headingDesktop,
  headingMobile,
  products,
  productsColor,
  product,
  productColor,
  productImageOverride,
  productsImageOverride,
  sectionType,
}) => {
  const { devices } = useDetectDevices()

  return (
    <StyledSection>
      <TextSection>
        {devices.desktop && (
          <Title>{documentToReactComponents(headingDesktop?.json)}</Title>
        )}
        {devices.mobile && (
          <Title>{documentToReactComponents(headingMobile?.json)}</Title>
        )}
        {devices.desktop && (
          <TextDescription>
            {documentToReactComponents(textDesktop?.json)}
          </TextDescription>
        )}
        {devices.mobile && (
          <TextDescription>
            {documentToReactComponents(textMobile?.json)}
          </TextDescription>
        )}
      </TextSection>
      <Container>
        <LeftContainer>
          {devices.desktop && (
            <ProductItem>
              <PairWithThumbnail
                key={product.id}
                contentfulProduct={product}
                title={product?.title || product?.shopifyProduct?.title}
                staticPrice={
                  product?.shopifyProduct?.variants?.edges?.[0]?.node?.price
                }
                startingColor={productColor?.name}
                tracker='landing_page__featured_product_image_with_products__featured_product'
                colorObj={productColor}
                gender={product.gender}
                imageOverride={productImageOverride}
                addToCartTrackerSection={sectionType}
              />
            </ProductItem>
          )}
        </LeftContainer>
        <StyledImageWithLink
          imageDesktopLink={imageDesktopLink}
          imageMobile={imageMobile}
          imageDesktop={imageDesktop}
          imageMobileLink={imageMobileLink}
          tracker='landing_page__featured_product_image_with_products__image_1'
        />
      </Container>
      <ProductsList
        products={products}
        productsColor={productsColor}
        productsImageOverride={productsImageOverride}
        addToCartTrackerSection={sectionType}
        tracker='landing_page__featured_product_image_with_products'
      />
    </StyledSection>
  )
}

export default FeaturedProductImageWithProducts
