import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  text-align: center;
  padding: ${mobileVw(30)} ${mobileVw(30)} ${mobileVw(54)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(82)} ${desktopVw(366)} ${desktopVw(82)};
  }
`

const PageHeader = styled.div`
  text-align: center;
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    margin-bottom: ${mobileVw(18)};
    font-size: ${mobileVw(54)};
    line-height: ${mobileVw(60)};
    font-weight: 400;
    text-align: center;
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(95)};
      margin-bottom: ${desktopVw(30)};
      line-height: ${desktopVw(60)};
    }
  }
`

const Text = styled.div`
  font-size: ${mobileVw(14)};
  line-height: ${mobileVw(20)};
  padding: 0 ${mobileVw(6)};
  h4 {
    font-size: ${mobileVw(20)};
    line-height: ${mobileVw(25)};
    margin-bottom: ${mobileVw(10)};
    font-family: ${props => props.theme.fonts.primary};
  }
  p {
    padding: 0 ${mobileVw(20)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    h4 {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
      margin-bottom: ${desktopVw(10)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      padding: 0 ${desktopVw(80)};
    }
  }
`
const SVGDesktop = styled.img`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block !important;
    margin: auto;
    margin-top: ${desktopVw(66)};
  }
`
const SVGImageMobile = styled.img`
  display: block;
  margin-top: ${mobileVw(57)};
  width: 100%;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const BannerWithImage = ({
  headingDesktop,
  headingMobile,
  textDesktop,
  textMobile,
  imageMobile,
  imageDesktop,
  backgroundColor,
}) => {
  const { devices } = useDetectDevices()
  return (
    <StyledWrapper bgColor={backgroundColor?.hexcode}>
      <PageHeader>
        {devices.desktop
          ? documentToReactComponents(headingDesktop?.json)
          : documentToReactComponents(headingMobile?.json)}
      </PageHeader>
      <Text>
        {devices.desktop
          ? documentToReactComponents(textDesktop?.json)
          : documentToReactComponents(textMobile?.json)}
      </Text>
      <SVGDesktop src={imageDesktop?.file?.url} />
      <SVGImageMobile src={imageMobile?.file?.url} />
    </StyledWrapper>
  )
}

export default BannerWithImage
