import React from 'react'

import { ContentSize, getLinkHref } from '../../utils'
import Button from '../Button'
import { desktopVw } from '../../styles/utils'
// Styles
import styled from 'styled-components'

const StyledButton = styled(Button)`
  display: flex;
  justify-content: center;
  align-items: center;
  background: ${props => props.theme.colors.primary};
  color: ${props => props.theme.colors.secondary};
  width: ${props => props.buttonMobileStyle.width}px;
  height: ${props => props.buttonMobileStyle.height}px;
  font-size: ${props => props.buttonMobileStyle.fontSize}px;
  line-height: ${props => props.buttonMobileStyle.lineHeight}px;
  margin: 0px auto 20px;
  font-weight: 500;
  padding: 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${props => desktopVw(props.buttonDesktopStyle.width)};
    height: ${props => desktopVw(props.buttonDesktopStyle.height)};
    font-size: ${props => desktopVw(props.buttonDesktopStyle.fontSize)};
    line-height: ${props => desktopVw(props.buttonDesktopStyle.lineHeight)};
    margin: 0 auto auto;
    font-weight: normal;
    text-transform: uppercase;
  }
`

const StyledButtons = styled.div`
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    justify-content: space-evenly;
  }

  .rating__star {
    width: 29px;
    height: 29px;
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      width: ${desktopVw(33)};
      height: ${desktopVw(32)};
    }
  }
`

const ButtonContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
`

const ButtonsSection = ({
  buttonList,
  buttonSizeDesktop,
  buttonSizeMobile,
}) => (
  <StyledButtons>
    {buttonList.map(button => (
      <ButtonContainer>
        <StyledButton
          to={getLinkPath(button)}
          href={getLinkHref(button)}
          target={'_blank'}
          trackParams={{
            trackInternal: true,
            trackGA: true,
            category: 'landing_page',
            action: 'button_click',
            label: button.text,
            value: button.url ? getLinkHref(button) : getLinkPath(button),
          }}
          buttonDesktopStyle={
            SectionStyle['button']['desktop'][
              buttonSizeDesktop ? buttonSizeDesktop : ContentSize.MEDIUM
            ]
          }
          buttonMobileStyle={
            SectionStyle['button']['mobile'][
              buttonSizeMobile ? buttonSizeMobile : ContentSize.MEDIUM
            ]
          }
        >
          {button.text}
        </StyledButton>
      </ButtonContainer>
    ))}
  </StyledButtons>
)

const SectionStyle = {
  button: {
    desktop: {
      [ContentSize.MEDIUM]: {
        width: 266,
        height: 48,
        fontSize: 18,
        lineHeight: 20,
      },
      [ContentSize.LARGE]: {
        width: 447,
        height: 96,
        fontSize: 24,
        lineHeight: 25,
      },
    },
    mobile: {
      [ContentSize.MEDIUM]: {
        width: 216,
        height: 42,
        fontSize: 18,
        lineHeight: 20,
      },
      [ContentSize.LARGE]: {
        width: 320,
        height: 72,
        fontSize: 20,
        lineHeight: 25,
      },
    },
  },
}

const getLinkPath = link => {
  if (!link) return undefined
  if (link.entry) return `/${link.entry.slug}`
  if (link.slug) return `/${link.slug}`
  return undefined
}

export default ButtonsSection
