import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import Image from 'gatsby-image'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import Button from '../Button'
import { getLinkPath, imageSources } from '../../utils'

const StyledWrapper = styled.div`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  position: relative;
  color: white;
  margin-bottom: ${mobileVw(34)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${mobileVw(0)};
    padding: ${desktopVw(40)};
  }
`

const StyledHeading = styled.div`
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    font-weight: 400;
    font-size: ${mobileVw(34)};
    line-height: ${mobileVw(34)};
    margin-bottom: ${mobileVw(20)};
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(64)};
      line-height: ${desktopVw(64)};
      margin-bottom: ${desktopVw(35)};
    }
  }
`

const StyledText = styled.div`
  font-size: ${mobileVw(14)};
  line-height: ${mobileVw(20)};
  margin-bottom: ${mobileVw(27)};
  margin-right: ${mobileVw(40)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(27)};
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      width: ${desktopVw(600)};
    }
  }
`

const StyledButton = styled(Button)`
  color: #fff;
  border-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: ${mobileVw(140)};
  height: ${mobileVw(40)};
  &:hover {
    border-color: #fff;
    background: transparent;
  }
  span {
    font-weight: normal;
    font-size: ${mobileVw(14)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    height: ${desktopVw(40)};
    width: ${desktopVw(140)};
    span {
      font-size: ${desktopVw(14)};
    }
  }
`

const StyledImage = styled(Image)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
  }
`

const StyleDesc = styled.div`
  position: absolute;
  top: ${mobileVw(48)};
  padding: 0 ${mobileVw(33)} 0 ${mobileVw(30)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${desktopVw(194)};
    left: ${desktopVw(120)};
    padding: 0;
  }
`

const DiscoverOurFactories = ({
  button,
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  textDesktop,
  textMobile,
  backgroundColor,
}) => {
  const { devices } = useDetectDevices()
  return (
    <StyledWrapper bgColor={backgroundColor?.hexcode}>
      <StyledImage fluid={imageSources(imageMobile, imageDesktop)} />
      <StyleDesc>
        <StyledHeading>
          {devices.desktop
            ? documentToReactComponents(headingDesktop?.json)
            : documentToReactComponents(headingMobile?.json)}
        </StyledHeading>
        <StyledText>
          {devices.desktop
            ? documentToReactComponents(textDesktop?.json)
            : documentToReactComponents(textMobile?.json)}
        </StyledText>
        <StyledButton to={getLinkPath(button)}>{button.text}</StyledButton>
      </StyleDesc>
    </StyledWrapper>
  )
}

export default DiscoverOurFactories
