import React from 'react'
import styled from 'styled-components'

import { desktopVw, mobileVw } from '../../../styles/utils'
import useDetectDevices from '../../../hooks/useDetectDevices'
import PairWithThumbnail from '../../PairWithThumbnail'

const ProductItem = styled.div`
  width: ${mobileVw(262)};

  .pair__with__thumbnail_image_wrapper {
    width: ${mobileVw(262)};
    height: ${mobileVw(327)};
  }

  padding: 0 6.5px;

  &:first-child {
    padding-left: 13px;
  }

  &:last-child {
    padding-right: 13px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: unset;
    width: ${desktopVw(403)};
    &:first-child {
      padding-left: unset;
    }

    &:last-child {
      padding-right: unset;
    }

    .pair__with__thumbnail_image_wrapper {
      width: ${desktopVw(403)};
      height: ${desktopVw(504)};
    }
  }
`
const ProductsListMobile = styled.div`
  display: flex;
  overflow-x: scroll;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar {
    display: none;
  }
`
const ProductsLisDesktop = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    justify-content: space-between;
    padding: 0 ${desktopVw(77)};
  }
`

export function ProductsList({
  products,
  productsImageOverride,
  productsColor,
  addToCartTrackerSection,
  tracker,
}) {
  const { devices } = useDetectDevices()

  function renderProductsList() {
    return (
      <>
        {products.map((contentfulProduct, cpIdx) => (
          <ProductItem>
            <PairWithThumbnail
              key={contentfulProduct.id}
              contentfulProduct={contentfulProduct}
              title={
                contentfulProduct?.title ||
                contentfulProduct?.shopifyProduct?.title
              }
              staticPrice={
                contentfulProduct?.shopifyProduct?.variants?.edges?.[0]?.node
                  ?.price
              }
              startingColor={productsColor[cpIdx]?.name}
              tracker={tracker ? `${tracker}__${cpIdx}` : ''}
              colorObj={productsColor[cpIdx]}
              gender={contentfulProduct.gender}
              imageOverride={productsImageOverride?.[cpIdx]}
              addToCartTrackerSection={addToCartTrackerSection}
            />
          </ProductItem>
        ))}
      </>
    )
  }

  return (
    <>
      {devices.mobile && (
        <ProductsListMobile>{renderProductsList()}</ProductsListMobile>
      )}
      {devices.desktop && (
        <ProductsLisDesktop>{renderProductsList()}</ProductsLisDesktop>
      )}
    </>
  )
}
