import React from 'react'

import { graphql } from 'gatsby'
import LandingPageContainer from '../components/LandingPageContainer'
import SEO from '../components/SEO'

const LandingPage = ({ data }) => {
  const { sections } = data.contentfulPage
  const page = data.contentfulPage
  const sectionMargin = parseInt(page.sectionMargin, 2) >= 0 ? 0 : 30 // Default gap between sections is 30
  return (
    <>
      <SEO
        title={page.seoTitle}
        description={page.seoDescription}
        image={page.metaImage ? page.metaImage.fluid.src : null}
        slug={page.slug}
      />
      <LandingPageContainer
        sectionList={sections}
        sectionMargin={sectionMargin}
      />
    </>
  )
}

export default LandingPage

