import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import Video from '../Video'

const Wrapper = styled.div`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  flex-direction: ${props =>
    props.mobileTextBottom ? 'column-reverse' : 'column'};
  display: flex;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: ${props => (props.textLeft ? 'row' : 'row-reverse')};
  }
`

const LeftWrapper = styled.div`
  flex: 1;
  padding: ${mobileVw(60)} ${mobileVw(30)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(194)} ${desktopVw(123)} ${desktopVw(134)};
  }
`

const SVGDesktop = styled.img`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    width: 100%;
    margin-bottom: ${desktopVw(72)};
  }
`
const SVGImageMobile = styled.img`
  display: block;
  width: 100%;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const RightWrapper = styled.div``

const StyledVideo = styled(Video)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(600)};
    min-height: ${desktopVw(1000)};
  }
`

const Heading = styled.div`
  margin-bottom: ${mobileVw(58)};
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${mobileVw(29)};
    line-height: ${mobileVw(29)};
    font-weight: 400;
    white-space: normal;
    b {
      font-size: ${mobileVw(40)};
      font-weight: 500;
    }
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(50)};
    h1 {
      font-size: ${desktopVw(38)};
      line-height: ${desktopVw(40)};
      margin: 0;
      b {
        font-size: ${desktopVw(55)};
        font-weight: 500;
      }
      /* display: flex; */
      /* align-items: center; */
    }
  }
`

const TextWrapper = styled.div`
  width: ${mobileVw(250)};
  h4 {
    font-size: ${mobileVw(20)};
    line-height: ${mobileVw(25)};
    margin-bottom: ${mobileVw(8)};
    font-family: ${props => props.theme.fonts.primary};
  }
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
    padding-bottom: ${mobileVw(8)};
    &:last-of-type {
      padding-bottom: 0%;
    }
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 100%;
    h4 {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
      margin-bottom: ${desktopVw(8)};
    }
    p {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
      padding-bottom: ${desktopVw(8)};
      &:last-of-type {
        padding-bottom: 0;
      }
    }
  }
`

const CardWrapper = styled.div``

const Cardheading = styled.div`
  margin-top: ${mobileVw(49)};
  margin-bottom: ${mobileVw(22)};
  font-size: ${mobileVw(20)};
  line-height: ${mobileVw(25)};
  font-weight: 500;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(50)};
    margin-bottom: ${desktopVw(30)};
    font-size: ${desktopVw(20)};
    line-height: ${desktopVw(25)};
  }
`

const PricingComparison = styled.div`
margin-top: ${mobileVw(55)};
@media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(50)};
    /* margin-bottom: ${desktopVw(30)}; */
  }
`

const PriceComparisonheader = styled.div`
  font-size: ${mobileVw(20)};
  line-height: ${mobileVw(25)};
  font-weight: 500;
  margin-bottom: -${mobileVw(20)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(20)};
    line-height: ${desktopVw(25)};
    margin-bottom: -${desktopVw(20)};
  }
`

const PriceComparisonChart = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
`

const PriceChart = styled.div`
  width: ${mobileVw(63)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(100)};
  }
`
const PriceGraph = styled.div`
  height: ${props => props.height};
  background-color: ${props => props.bgcolor};
`

const Desc = styled.div`
  color: ${props => props.bgcolor};
  text-align: center;
  margin-top: ${mobileVw(22)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(22)};
  }
`
const StyledSuper = styled.sup``

const StyledPriceSpan = styled.span``

const Price = styled.div`
  display: flex;
  justify-content: center;
  font-size: ${mobileVw(14)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(14)};
  }
`
const VendorName = styled.div`
  font-size: ${mobileVw(10)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const TextWithRightVideo = ({
  cards,
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  backgroundColor,
  card,
  textDesktop,
  textMobile,
  textSectionDesktopLayout,
  textSectionMobileLayout,
}) => {
  const { devices } = useDetectDevices()
  return (
    <Wrapper
      bgColor={backgroundColor?.hexcode}
      mobileTextBottom={textSectionMobileLayout === 'Content Bottom'}
      textLeft={textSectionDesktopLayout === 'Content Left'}
    >
      <LeftWrapper>
        <Heading>
          {devices.desktop
            ? documentToReactComponents(headingDesktop?.json)
            : documentToReactComponents(headingMobile?.json)}
        </Heading>
        <TextWrapper>
          {devices.desktop
            ? documentToReactComponents(textDesktop?.json)
            : documentToReactComponents(textMobile?.json)}
        </TextWrapper>
        <CardWrapper>
          <Cardheading>{card?.title?.title}</Cardheading>
          <SVGDesktop src={card?.imageDesktop?.file?.url} />
          <SVGImageMobile src={card?.image?.file?.url} />
        </CardWrapper>
        <PricingComparison>
          <PriceComparisonheader>Pricing Comparison</PriceComparisonheader>
          <PriceComparisonChart>
            {cards.map(it => {
              return (
                <PriceChart>
                  <PriceGraph
                    height={`${parseFloat(it.price) * 0.61}px`}
                    bgcolor={it?.backgroundColor?.hexcode}
                  />
                  <Desc bgcolor={it?.backgroundColor?.hexcode}>
                    <Price>
                      <StyledSuper>$</StyledSuper>
                      <StyledPriceSpan>{parseFloat(it.price)}</StyledPriceSpan>
                    </Price>
                    <VendorName>{it.smallText}</VendorName>
                  </Desc>
                </PriceChart>
              )
            })}
          </PriceComparisonChart>
        </PricingComparison>
      </LeftWrapper>
      <RightWrapper>
        <StyledVideo
          url={
            devices.desktop ? imageDesktop?.file?.url : imageMobile?.file?.url
          }
          lazyLoad={false}
        />
      </RightWrapper>
    </Wrapper>
  )
}

export default TextWithRightVideo
