import React from 'react'
import styled from 'styled-components'
import { desktopVw, mobileVw } from '../../styles/utils'
import ScaledImage from '../ScaledImage'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  padding: 0;
  margin-top: ${mobileVw(-101)};
  display: flex;
  align-items: center;
  overflow: hidden;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    justify-content: space-between;
    margin-top: ${desktopVw(-214)};
    padding: 0 ${desktopVw(160)};
  }
`

const StyledScaledImage = styled(ScaledImage)`
  width: ${props => (props.even ? mobileVw(113) : mobileVw(151))};
  height: ${props => (props.even ? mobileVw(154) : mobileVw(202))};
  display: inline-block;
  &:first-of-type {
    margin-left: -${mobileVw(30)};
    margin-right: ${mobileVw(30)};
  }
  &:last-of-type {
    margin-left: ${mobileVw(30)};
    margin-right: -${mobileVw(30)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${props => (props.even ? desktopVw(240) : desktopVw(318))};
    height: ${props => (props.even ? desktopVw(325) : desktopVw(428))};
    display: block;
    &:first-of-type {
      margin-left: 0;
      margin-right: 0;
    }
    &:last-of-type {
      margin-left: 0;
      margin-right: 0;
    }
  }
`

const ImageRowOverlapped = ({ productsImageOverride, backgroundColor }) => {
  return (
    <StyledWrapper bgColor={backgroundColor?.hexcode}>
      {productsImageOverride.map((imageObj, index) => (
        <StyledScaledImage
          even={index % 2 === 0}
          singleImage={false}
          fluid={imageObj?.fluid}
        />
      ))}
    </StyledWrapper>
  )
}

export default ImageRowOverlapped
