import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import Image from 'gatsby-image'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import { imageSources } from '../../utils'
import ScaledImage from '../ScaledImage'

const StyledBanner = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    height: ${desktopVw(1312)};
    padding: ${desktopVw(30)} ${desktopVw(40)};
    margin: -${props => desktopVw(props.theme.header.desktop.height)} 0 0;
    box-sizing: border-box;
  }
`

const ScaledImageDesktop = styled(ScaledImage)`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const ScaledImageMobile = styled(ScaledImage)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const DescWrap = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: absolute;
    top: ${desktopVw(321)};
    left: ${desktopVw(160)};
  }
`

const Heading = styled.div`
  height: ${mobileVw(114)};
  align-items: center;
  display: flex;
  justify-content: center;
  h1 {
    font-family: ${({ theme }) => theme.fonts.secondary};
    font-size: ${mobileVw(54)};
    line-height: ${mobileVw(60)};
    font-weight: 400;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    height: auto;
    h1 {
      font-size: ${desktopVw(115)};
      margin-bottom: ${desktopVw(40)};
      line-height: ${desktopVw(80)};
    }
  }
`

const TextWrapper = styled.div`
  position: relative;
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
    margin-top: ${mobileVw(15)};
    padding: 0 ${mobileVw(12)};
  }
  h4 {
    font-size: ${mobileVw(18)};
    line-height: ${mobileVw(20)};
    font-family: ${({ theme }) => theme.fonts.primary};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(390)};
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      margin-top: ${desktopVw(26)};
      padding: 0;
    }

    h4 {
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(25)};
    }
  }
`

const Text = styled.div`
  position: absolute;
  top: ${mobileVw(57)};
  padding: 0 ${mobileVw(30)};
  text-align: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    top: 0;
    padding: 0;
    text-align: start;
  }
`

const BannerLowerOverlapped = ({
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  textDesktop,
  textMobile,
}) => {
  const { devices } = useDetectDevices()
  return (
    <StyledBanner>
      <DescWrap>
        <Heading>
          {devices.desktop
            ? documentToReactComponents(headingDesktop?.json)
            : documentToReactComponents(headingMobile?.json)}
        </Heading>
        <TextWrapper>
          <ScaledImageMobile
            fluid={imageSources(imageMobile, imageDesktop)}
            style={{
              zIndex: '-1',
            }}
          />
          <Text>
            {devices.desktop
              ? documentToReactComponents(textDesktop?.json)
              : documentToReactComponents(textMobile?.json)}
          </Text>
        </TextWrapper>
      </DescWrap>
      <ScaledImageDesktop
        fluid={imageSources(imageMobile, imageDesktop)}
        style={{
          position: 'absolute',
          left: '0',
          right: '0',
          bottom: '0',
          top: '0',
          zIndex: '-1',
        }}
      />
    </StyledBanner>
  )
}

export default BannerLowerOverlapped
