import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import Button from '../Button'
import { getLinkPath } from '../../utils'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  text-align: center;
  padding: ${mobileVw(31)} ${mobileVw(25)} ${mobileVw(36)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(91)} ${desktopVw(363)};
  }
`

const PageHeader = styled.div`
  text-align: center;
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    margin-bottom: ${mobileVw(25)};
    font-size: ${mobileVw(30)};
    line-height: ${mobileVw(30)};
    font-weight: 400;
    /* white-space: nowrap; */
    text-align: center;
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      font-size: ${desktopVw(60)};
      margin-bottom: ${desktopVw(21)};
      line-height: ${desktopVw(72)};
    }
  }
`

const Text = styled.div`
  font-size: ${mobileVw(14)};
  line-height: ${mobileVw(20)};
  padding: 0 ${mobileVw(6)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(14)};
    line-height: ${desktopVw(20)};
    padding: 0 ${desktopVw(50)};
  }
`

const StyledButton = styled(Button)`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  height: ${mobileVw(40)};
  padding: 0 ${mobileVw(16)};
  min-width: ${mobileVw(90)};
  span {
    font-weight: normal;
    font-size: ${mobileVw(14)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(15)};
    height: ${desktopVw(40)};
    min-width: ${desktopVw(149)};
    span {
      font-size: ${desktopVw(14)};
    }
  }
`

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: ${mobileVw(31)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(47)};
  }
`

const BannerWithCenterAlignTextAndCTA = ({
  headingDesktop,
  headingMobile,
  textDesktop,
  textMobile,
  backgroundColor,
  metaTags,
  button,
}) => {
  const { devices } = useDetectDevices()
  if (metaTags) {
    if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && !devices.desktop)
      return null
    if (metaTags.includes('SHOW_ONLY_ON_MOBILE') && !devices.mobile) return null
  }
  return (
    <StyledWrapper bgColor={backgroundColor?.hexcode}>
      <PageHeader>
        {devices.desktop
          ? documentToReactComponents(headingDesktop?.json)
          : documentToReactComponents(headingMobile?.json)}
      </PageHeader>
      <Text>
        {devices.desktop
          ? documentToReactComponents(textDesktop?.json)
          : documentToReactComponents(textMobile?.json)}
      </Text>
      <ButtonContainer>
        <StyledButton to={getLinkPath(button)}>{button.text}</StyledButton>
      </ButtonContainer>
    </StyledWrapper>
  )
}

export default BannerWithCenterAlignTextAndCTA
