import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import Video from '../Video'
import { getLinkPath } from '../../utils'
import Button from '../Button'

const Wrapper = styled.div`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  flex-direction: ${props =>
    props.mobileTextBottom ? 'column-reverse' : 'column'};
  display: flex;
  position: relative;
  padding: ${mobileVw(14)} ${mobileVw(26)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: ${props => (props.textLeft ? 'row' : 'row-reverse')};
    padding: ${desktopVw(32)} ${desktopVw(83)} ${desktopVw(340)}
      ${desktopVw(160)};
  }
`

const LeftWrapper = styled.div`
  flex: 1;
  padding: ${mobileVw(60)} 0 ${mobileVw(168)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    padding-left: ${desktopVw(160)};
    padding-top: ${desktopVw(27)};
  }
`

const RightWrapper = styled.div``

const StyledVideo = styled(Video)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(560)};
    height: ${desktopVw(720)};
  }
`

const Heading = styled.div`
  /* margin-bottom: ${mobileVw(63)}; */
  p {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${mobileVw(30)};
    line-height: ${mobileVw(28)};
    font-weight: 400;
    white-space: normal;
    text-align: center;
    /* padding: 0 ${mobileVw(32)}; */
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(53)};
    p {
      font-size: ${desktopVw(60)};
      line-height: ${desktopVw(60)};
      margin: 0;
      text-align: left;
      /* padding: 0; */
    }
  }
`

const TextWrapper = styled.div`
  text-align: center;
  padding: 0 ${mobileVw(5)};
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
    padding-bottom: 0;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 100%;
    text-align: left;
    padding: 0;
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      padding-bottom: 0;
      &:last-of-type {
        padding-bottom: 0;
      }
    }
  }
`

const CardWrapper = styled.div`
  margin-top: ${mobileVw(63)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(342)};
    margin-top: ${desktopVw(53)};
  }
`

const Cardheading = styled.div`
  margin-bottom: ${mobileVw(12)};
  font-size: ${mobileVw(20)};
  line-height: ${mobileVw(25)};
  font-weight: 500;
  text-align: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(31)};
    font-size: ${desktopVw(20)};
    line-height: ${desktopVw(25)};
    text-align: left;
  }
`

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: ${mobileVw(33)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(31)};
    justify-content: left;
  }
`

const StyledButton = styled(Button)`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  height: ${mobileVw(38)};
  padding: 0 ${mobileVw(16)};
  min-width: 90px;
  span {
    font-weight: normal;
    font-size: ${mobileVw(14)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(15)};
    height: ${desktopVw(40)};
    span {
      font-size: ${desktopVw(14)};
    }
  }
`

const VideoWithTextSectionBtn = ({
  cards,
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  backgroundColor,
  textSectionDesktopLayout,
  textSectionMobileLayout,
}) => {
  const { devices } = useDetectDevices()
  return (
    <Wrapper
      bgColor={backgroundColor?.hexcode}
      mobileTextBottom={textSectionMobileLayout === 'Content Bottom'}
      textLeft={textSectionDesktopLayout === 'Content Left'}
    >
      <LeftWrapper>
        <Heading>
          {devices.desktop
            ? documentToReactComponents(headingDesktop?.json)
            : documentToReactComponents(headingMobile?.json)}
        </Heading>
        {cards.map(cardData => {
          return (
            <CardWrapper>
              <Cardheading>{cardData?.title?.title}</Cardheading>
              <TextWrapper>
                {documentToReactComponents(cardData?.text?.json)}
              </TextWrapper>
              {cardData?.button && (
                <ButtonContainer>
                  <StyledButton to={getLinkPath(cardData?.button)}>
                    {cardData?.button?.text}
                  </StyledButton>
                </ButtonContainer>
              )}
            </CardWrapper>
          )
        })}
      </LeftWrapper>
      <RightWrapper>
        <StyledVideo
          url={
            devices.desktop ? imageDesktop?.file?.url : imageMobile?.file?.url
          }
          lazyLoad={false}
        />
      </RightWrapper>
    </Wrapper>
  )
}

export default VideoWithTextSectionBtn
