import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import Image from 'gatsby-image'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'

const StyledImagePrimary = styled(Image)`
width: ${mobileVw(184)};
  height: ${mobileVw(268)};
  position: absolute !important;
    top: ${mobileVw(42)};
    left: ${mobileVw(-45)};
@media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
  width: ${desktopVw(330)};
  height: ${desktopVw(479)};
  /* position: absolute !important; */
    top: ${desktopVw(75)};
    left: ${desktopVw(-80)};
}
}

`
const StyledImageSeconday = styled(Image)`
  width: ${mobileVw(268)};
  height: ${mobileVw(352)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(480)};
    height: ${desktopVw(628)};
  }
`

const StyledBanner = styled.div`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  padding: ${mobileVw(57)} ${mobileVw(29)} ${mobileVw(57)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    padding: ${desktopVw(426)} ${desktopVw(80)} ${desktopVw(80)};
    align-items: center;
  }
`

const LeftWrapper = styled.div`
  flex: 1;
`

const RightWrapper = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex: 1;
    justify-content: flex-end;
  }
`

const TextSection = styled.div`
  text-align: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(146)};
    width: ${desktopVw(616)};
    &:last-of-type {
      margin-bottom: 0;
    }
  }
`

const Heading = styled.div`
  font-family: ${({ theme }) => theme.fonts.secondary};
  font-size: ${mobileVw(30)};
  line-height: ${mobileVw(30)};
  margin-bottom: ${mobileVw(13)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(60)};
    line-height: ${desktopVw(64)};
    margin-bottom: ${desktopVw(22)};
  }
`

const Text = styled.div`
  font-size: ${mobileVw(14)};
  line-height: ${mobileVw(20)};
  /* padding: 0 ${mobileVw(18)}; */
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(14)};
    line-height: ${desktopVw(20)};
    padding: 0 ${desktopVw(65)};
  }
`

const ImgWrapper = styled.div`
  position: relative;
`

const MobileImageWrapper = styled.div`
  display: block;
  position: relative;
  margin-left: ${mobileVw(45)};
  margin-top: ${mobileVw(62)};
  margin-bottom: ${mobileVw(57)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const BannerUpperOverlapped = ({
  headingDesktop,
  headingMobile,
  backgroundColor,
  cards,
  imageDesktop,
  secondImageDesktop,
  metaTags,
}) => {
  const { devices } = useDetectDevices()
  if (metaTags) {
    if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && !devices.desktop)
      return null
    if (metaTags.includes('SHOW_ONLY_ON_MOBILE') && !devices.mobile) return null
  }
  return (
    <StyledBanner bgColor={backgroundColor?.hexcode}>
      <LeftWrapper>
        {cards.map((it, index) => {
          return (
            <>
              <TextSection>
                <Heading>{it.title.title}</Heading>
                <Text>{documentToReactComponents(it?.text?.json)}</Text>
              </TextSection>
              {index === 0 && (
                <MobileImageWrapper>
                  <StyledImageSeconday fluid={secondImageDesktop?.fluid} />
                  <StyledImagePrimary fluid={imageDesktop?.fluid} />
                </MobileImageWrapper>
              )}
            </>
          )
        })}
      </LeftWrapper>
      <RightWrapper>
        <ImgWrapper>
          <StyledImageSeconday fluid={secondImageDesktop?.fluid} />
          <StyledImagePrimary fluid={imageDesktop?.fluid} />
        </ImgWrapper>
      </RightWrapper>
    </StyledBanner>
  )
}

export default BannerUpperOverlapped
