import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import Image from 'gatsby-image'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import { imageSources } from '../../utils'

const CardWrapper = styled.div`
  background: white;
  padding: ${mobileVw(59)} ${mobileVw(27)} ${mobileVw(65)} ${mobileVw(27)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    margin: ${desktopVw(-444)} ${desktopVw(80)} ${desktopVw(0)} ${desktopVw(80)};
    position: absolute;
    padding: ${desktopVw(80)} ${desktopVw(26)} ${desktopVw(80)} ${desktopVw(80)};
  }
`

const LeftWrapper = styled.div`
  flex: 1;
`

const StyledImageDesktop = styled(Image)`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(480)};
    height: ${desktopVw(632)};
    display: block;
  }
`

const StyledImageMobile = styled(Image)`
  display: block;
  width: ${mobileVw(314)};
  height: ${mobileVw(412)};
  margin-bottom: ${mobileVw(56)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const RightWrapper = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(0)} ${desktopVw(26)} ${desktopVw(0)} ${desktopVw(80)};
    margin: auto;
  }
`

const HeadingWrapper = styled.div`
  margin-bottom: ${mobileVw(63)};
  text-align: center;
  h1 {
    font-family: ${({ theme }) => theme.fonts.secondary};
    font-weight: 400;
    font-size: ${mobileVw(30)};
    line-height: ${mobileVw(30)};
    margin-bottom: ${mobileVw(12)};
  }
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(75)};
    text-align: start;
    h1 {
      font-size: ${desktopVw(55)};
      line-height: ${desktopVw(64)};
      margin-bottom: ${desktopVw(30)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      padding-right: ${desktopVw(30)};
    }
  }
`
const TextBlocksWrapper = styled.div`
  display: flex;
  flex-direction: column;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
  }
`
const TextBlocks = styled.div`
  text-align: center;
  margin-bottom: ${mobileVw(34)};
  &:last-of-type {
    margin-bottom: 0;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(200)};
    margin-right: ${desktopVw(80)};
    text-align: start;
  }
`

const TextBlockTitle = styled.div`
  font-size: ${mobileVw(18)};
  line-height: ${mobileVw(25)};
  font-weight: 500;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(20)};
    line-height: ${desktopVw(25)};
  }
`

const TextBlockDesc = styled.div`
  font-size: ${mobileVw(14)};
  line-height: ${mobileVw(20)};
  margin-top: ${mobileVw(15)};
  padding: 0px ${props => (props.last ? mobileVw(10) : '0')};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(14)};
    line-height: ${desktopVw(20)};
    margin-top: ${desktopVw(21)};
    padding: 0 ${props => (props.last ? '0' : desktopVw(30))} 0 0;
  }
`

const TopAndBottomOverlapped = ({
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  textDesktop,
  textMobile,
  cards,
  metaTags,
}) => {
  const { devices } = useDetectDevices()
  if (metaTags) {
    if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && !devices.desktop)
      return null
    if (metaTags.includes('SHOW_ONLY_ON_MOBILE') && !devices.mobile) return null
  }
  return (
    <CardWrapper>
      <LeftWrapper>
        <StyledImageDesktop fluid={imageSources(imageMobile, imageDesktop)} />
      </LeftWrapper>
      <RightWrapper>
        <HeadingWrapper>
          {devices.desktop
            ? documentToReactComponents(headingDesktop?.json)
            : documentToReactComponents(headingMobile?.json)}
          {devices.desktop
            ? documentToReactComponents(textDesktop?.json)
            : documentToReactComponents(textMobile?.json)}
        </HeadingWrapper>
        <StyledImageMobile fluid={imageSources(imageMobile, imageDesktop)} />
        <TextBlocksWrapper>
          {cards.map((it, index) => {
            return (
              <TextBlocks>
                <TextBlockTitle>{it?.title?.title}</TextBlockTitle>
                <TextBlockDesc last={index === cards.length - 1}>
                  {documentToReactComponents(it?.text?.json)}
                </TextBlockDesc>
              </TextBlocks>
            )
          })}
        </TextBlocksWrapper>
      </RightWrapper>
    </CardWrapper>
  )
}

export default TopAndBottomOverlapped
