import ImageOverlappedWithCard from './ImageOverlappedWithCard'
import ImageCardWithAlignText from './ImageCardWithAlignText'
import ImageCardWithOverlappedImage from './ImageCardWithOverlappedImage'
import BannerWithCenterAlignTextAndCTA from './BannerCenterAlignTextAndCTA'

const SectionTypeSectionMap = {
  Buttons: ButtonsSection,
  FAQ: FAQSection,
  Hero: HeroSection,
  Reviews: ReviewsSection,
  'Image with Text': LandingPageImageWithTextSection,
  'Dynamic Product List': '',
  'Image List': '',
  'Product List': '',
  Text: TextSection,
  'Trust Badges': TrustBadgesSection,
  'Affiliate Badges': AffiliateBadgesSection,
  'Cashmere Badges': CashmereBadgesSection,
  'Hero With Card': HeroWithCardSection,
  'Images Side By Side With Products': ImagesSideBySideWithProductsSection,
  'Featured Product And Image With Products': FeaturedProductImageWithProducts,
  'Images Overlapped With Products': ImagesOverlappedWithProducts,
  'Best Sellers': BestSellersSection,
  'Background Video With Text': BackgroundVideoWithText,
  'How It Works': HowItWorks,
  'Text With Video': TextWithVideo,
  'Discover Our Factories': DiscoverOurFactories,
  'Banner With Overlapped': BannerOverlapped,
  'Top And Bottom Overlapped Card': TopAndBottomOverlapped,
  'Banner With Center Align Text': BannerWithCenterAlignText,
  'Video Overlapped With Text At Bottom': VideoOverlappedTextBottom,
  'Scrollable sticky Left': ScrollableSticky,
  'Mobile Image Carousel': MobileImageCarousel,
  'Image Card': MobileImageCard,
  'Text Section With Buttons': TextSectionWithButton,
  'Banner With Image': BannerWithImage,
  'Video With Text Section And Button': VideoWithTextSectionBtn,
  'Image Row Overlapped': ImageRowOverlapped,
  'Banner With Center Align Text And Icon': BannerWithCenterTextAndIcon,
  'Image Overlapped With Card': ImageOverlappedWithCard,
  'Image Row Cards': ImageRowCards,
  'Image Card With Aligned Text': ImageCardWithAlignText,
  'Image Card With Overlapped Image': ImageCardWithOverlappedImage,
  'Banner With Center Align Text And CTA': BannerWithCenterAlignTextAndCTA,
}

const LandingPageContainer = ({ sectionList, sectionMargin }) => {
  return (
    <>
      {sectionList.map((section, i) => {
        const Section = SectionTypeSectionMap[section.sectionType]
        return Section && renderSection(Section, section, sectionMargin, i)
      })}
    </>
  )
}

const renderSection = (Section, section, sectionMargin, index) => {
  return (
    <div style={{ marginBottom: `${sectionMargin}px` }}>
      <Section key={index} {...section} />
    </div>
  )
}

