import React from 'react'
import { Link } from 'gatsby'
import useDetectDevices from '../../../hooks/useDetectDevices'
import { getLinkPath } from '../../../utils'
import ScaledImage from '../../ScaledImage'

function getLinkWithTracker(imageDesktopLink, tracker) {
  const linkPath = new URL(
    `https://onequince.com${getLinkPath(imageDesktopLink)}`,
  )
  if (tracker) linkPath.searchParams.append('tracker', tracker)
  return linkPath.toString().split('https://onequince.com')[1]
}

export function ImageWithLink({
  imageDesktop,
  imageDesktopLink,
  imageMobile,
  imageMobileLink,
  tracker,
  className,
}) {
  const { devices } = useDetectDevices()

  return (
    <div className={className}>
      {devices.desktop &&
        imageDesktop &&
        (imageDesktopLink ? (
          <Link to={getLinkWithTracker(imageDesktopLink, tracker)}>
            <ScaledImage fluid={imageDesktop.fluid} />
          </Link>
        ) : (
          <ScaledImage fluid={imageDesktop.fluid} />
        ))}
      {devices.mobile &&
        imageMobile &&
        (imageMobileLink ? (
          <Link to={getLinkWithTracker(imageMobileLink, tracker)}>
            <ScaledImage fluid={imageMobile.fluid} />
          </Link>
        ) : (
          <ScaledImage fluid={imageMobile.fluid} />
        ))}
    </div>
  )
}
