import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import Video from '../Video'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
`

const StyledVideo = styled(Video)`
  height: ${mobileVw(319)};
  width: 100%;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(810)};
    /* width: ${`calc(100% - ${2 * parseFloat(desktopVw(80))}vw)`}; */
  }
`
const VideoWrapper = styled.div`
  padding-bottom: ${mobileVw(34)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(80)} ${desktopVw(147)};
  }
`
const TextWrapper = styled.div`
  background-color: white;
  text-align: center;
  position: relative;
  margin: 0 ${mobileVw(30)};
  margin-top: ${mobileVw(-130)};
  padding: ${mobileVw(35)} ${mobileVw(31)} ${mobileVw(33)} ${mobileVw(33)};
  h1 {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${mobileVw(34)};
    line-height: ${mobileVw(34)};
  }
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(200)};
    padding: ${desktopVw(26)} ${desktopVw(107)};
    padding-top: ${desktopVw(36)};
    margin-top: ${desktopVw(-120)};
    h1 {
      font-size: ${desktopVw(48)};
      line-height: ${desktopVw(48)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
    }
  }
`
const Heading = styled.div`
  margin-bottom: ${mobileVw(27)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(24)};
  }
`

const TextBlock = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(14)};
  }
`

const VideoOverlappedTextBottom = ({
  headingDesktop,
  headingMobile,
  imageDesktop,
  imageMobile,
  textDesktop,
  textMobile,
  backgroundColor,
}) => {
  const { devices } = useDetectDevices()
  return (
    <StyledWrapper bgColor={backgroundColor?.hexcode}>
      <VideoWrapper>
        <StyledVideo
          url={
            devices.desktop ? imageDesktop?.file?.url : imageMobile?.file?.url
          }
          lazyLoad={false}
        />
        <TextWrapper>
          <Heading>
            {devices.desktop
              ? documentToReactComponents(headingDesktop?.json)
              : documentToReactComponents(headingMobile?.json)}
          </Heading>
          <TextBlock>
            {devices.desktop
              ? documentToReactComponents(textDesktop?.json)
              : documentToReactComponents(textMobile?.json)}
          </TextBlock>
        </TextWrapper>
      </VideoWrapper>
    </StyledWrapper>
  )
}

export default VideoOverlappedTextBottom
