import React from 'react'
import styled from 'styled-components'
import { desktopVw, mobileVw } from '../../styles/utils'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import useDetectDevices from '../../hooks/useDetectDevices'
import { ProductsList } from './common/ProductsList'
import { ImageWithLink } from './common/ImageWithLink'

const StyledSection = styled.section``

const StyledImageWithLink = styled(ImageWithLink)`
  width: unset;
  height: ${mobileVw(445)};
  padding: 23px 11px 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(714)};
    height: ${desktopVw(897)};
    margin-right: ${desktopVw(46)};
    padding: 0;
  }
`

const Container = styled.div`
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 54px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    margin-bottom: ${desktopVw(80)};
  }
`

const RightContainer = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-top: ${desktopVw(80)};
  }
`

const TextSection = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(63)} ${desktopVw(43)};
  }
`

const Title = styled.div`
  text-align: center;
  padding: 0 79px;
  margin-bottom: 20px;
  p {
    font-family: ${props => props.theme.fonts.secondary};
    font-weight: 500;
    font-size: 85px;
    line-height: 76px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    text-align: left;
    margin-bottom: ${desktopVw(20)};
    padding: 0;
    p {
      font-size: ${desktopVw(85)};
      line-height: ${desktopVw(76)};
    }
  }
`

const TextDescription = styled.div`
  padding: 0 21px;
  text-align: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    text-align: left;
    padding: 0;
    max-width: ${desktopVw(440)};
    p {
      line-height: ${desktopVw(26)};
    }
  }
`

const SecondImageWithLink = styled(StyledImageWithLink)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(600)};
    height: ${desktopVw(754)};
  }
`

const ImagesSideBySideWithProductsSection = ({
  imageDesktop,
  imageDesktopLink,
  imageMobileLink,
  imageMobile,
  textDesktop,
  secondImageDesktop,
  secondImageDesktopLink,
  textMobile,
  headingDesktop,
  headingMobile,
  products,
  productsColor,
  productsImageOverride,
  sectionType,
}) => {
  const { devices } = useDetectDevices()

  return (
    <StyledSection>
      <Container>
        <StyledImageWithLink
          imageDesktopLink={imageDesktopLink}
          imageMobile={imageMobile}
          imageDesktop={imageDesktop}
          imageMobileLink={imageMobileLink}
          tracker='landing_page__image_side_by_side_with_products__image_1'
        />
        <RightContainer>
          <TextSection>
            {devices.desktop && (
              <Title>{documentToReactComponents(headingDesktop?.json)}</Title>
            )}
            {devices.mobile && (
              <Title>{documentToReactComponents(headingMobile?.json)}</Title>
            )}
            {devices.desktop && (
              <TextDescription>
                {documentToReactComponents(textDesktop?.json)}
              </TextDescription>
            )}
            {devices.mobile && (
              <TextDescription>
                {documentToReactComponents(textMobile?.json)}
              </TextDescription>
            )}
          </TextSection>
          {devices.desktop && (
            <SecondImageWithLink
              imageDesktopLink={secondImageDesktopLink}
              imageDesktop={secondImageDesktop}
              tracker='landing_page__image_side_by_side_with_products__image_2'
            />
          )}
        </RightContainer>
      </Container>
      <ProductsList
        products={products}
        productsColor={productsColor}
        productsImageOverride={productsImageOverride}
        addToCartTrackerSection={sectionType}
        tracker='landing_page__image_side_by_side_with_products'
      />
    </StyledSection>
  )
}

export default ImagesSideBySideWithProductsSection
