import React from 'react'

// Styles
import styled from 'styled-components'
import RichText from '../RichText'
import { desktopVw } from '../../styles/utils'
import { ContentSize } from '../../utils'
import { Heading2Style, TextSectionStyle } from './index'

const StyledRichText = styled(RichText)`
  padding: 0 24px;
  p {
    font-family: ${props => props.theme.fonts.primary};
    font-size: ${props => props.textMobileStyle.fontSize}px;
    line-height: ${props => props.textMobileStyle.lineHeight}px;
    min-height: ${props => props.textMobileStyle.lineHeight}px;
    margin-bottom: 0px;
  }
  h2 {
    font-family: ${props => props.theme.fonts.secondary};
    font-size: ${props => props.headingMobileStyle.fontSize}px;
    line-height: ${props => props.headingMobileStyle.lineHeight}px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(140)};
    p {
      font-size: ${props => desktopVw(props.textDesktopStyle.fontSize)};
      line-height: ${props => desktopVw(props.textDesktopStyle.lineHeight)};
      min-height: ${props => desktopVw(props.textDesktopStyle.lineHeight)};
    }
    h2 {
      font-size: ${props => desktopVw(props.headingDesktopStyle.fontSize)};
      line-height: ${props => desktopVw(props.headingDesktopStyle.lineHeight)};
    }
  }
`

const CenterAlignedRichText = styled(StyledRichText)`
  h1,
  h2,
  h3,
  h4,
  p {
    text-align: center;
  }
`

const LeftAlignedRichText = styled(StyledRichText)`
  h1,
  h2,
  h3,
  h4,
  p {
    text-align: left;
  }
`

const CenterAlignedRichTextMobile = styled(CenterAlignedRichText)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const CenterAlignedRichTextDesktop = styled(CenterAlignedRichText)`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const LeftAlignedRichTextMobile = styled(LeftAlignedRichText)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const LeftAlignedRichTextDesktop = styled(LeftAlignedRichText)`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const TextSection = ({
  textMobile,
  textDesktop,
  textSectionDesktopLayout,
  textSectionMobileLayout,
  headingSizeMobile,
  headingSizeDesktop,
  textSizeMobile,
  textSizeDesktop,
}) => {
  let RichTextDesktop = LeftAlignedRichTextDesktop
  let RichTextMobile = LeftAlignedRichTextMobile
  if (textSectionDesktopLayout === 'Content Center') {
    RichTextDesktop = CenterAlignedRichTextDesktop
  }
  if (textSectionMobileLayout === 'Content Center') {
    RichTextMobile = CenterAlignedRichTextMobile
  }
  return (
    <>
      <RichTextDesktop
        json={textDesktop.json}
        headingDesktopStyle={
          Heading2Style['desktop'][
            headingSizeDesktop ? headingSizeDesktop : ContentSize.MEDIUM
          ]
        }
        headingMobileStyle={
          Heading2Style['mobile'][
            headingSizeMobile ? headingSizeMobile : ContentSize.MEDIUM
          ]
        }
        textDesktopStyle={
          TextSectionStyle['desktop'][
            textSizeDesktop ? textSizeDesktop : ContentSize.MEDIUM
          ]
        }
        textMobileStyle={
          TextSectionStyle['mobile'][
            textSizeMobile ? textSizeMobile : ContentSize.MEDIUM
          ]
        }
      />
      <RichTextMobile
        json={textMobile.json}
        headingDesktopStyle={
          Heading2Style['desktop'][
            headingSizeDesktop ? headingSizeDesktop : ContentSize.MEDIUM
          ]
        }
        headingMobileStyle={
          Heading2Style['mobile'][
            headingSizeMobile ? headingSizeMobile : ContentSize.MEDIUM
          ]
        }
        textDesktopStyle={
          TextSectionStyle['desktop'][
            textSizeDesktop ? textSizeDesktop : ContentSize.MEDIUM
          ]
        }
        textMobileStyle={
          TextSectionStyle['mobile'][
            textSizeMobile ? textSizeMobile : ContentSize.MEDIUM
          ]
        }
      />
    </>
  )
}

export default TextSection
