import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import React from 'react'
import styled from 'styled-components'
import useDetectDevices from '../../hooks/useDetectDevices'
import { desktopVw, mobileVw } from '../../styles/utils'
import { imageSources } from '../../utils'
import ScaledImage from '../ScaledImage'

const StyledWrapper = styled.section`
  background-color: ${props => (props.bgColor ? props.bgColor : '#fff')};
  padding: 0;
  display: flex;
  align-items: center;
  overflow: hidden;
  justify-content: space-between;
  margin: 0 0 ${mobileVw(30)} 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(120)} 0;
  }
`

const StyledScaledImage = styled(ScaledImage)`
  width: ${props => (props.even ? mobileVw(63) : mobileVw(229))};
  height: ${mobileVw(374)};
  display: inline-block;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${props => (props.even ? desktopVw(423) : desktopVw(560))};
    height: ${desktopVw(748)};
    display: block;
  }
`

const SVGDesktop = styled.img`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block !important;
    z-index: 1;
    margin: auto;
  }
`

const TextCard = styled.div`
  position: absolute;
  z-index: 2;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  top: ${mobileVw(21)};
  p {
    b {
      font-size: ${mobileVw(48)};
      line-height: ${mobileVw(72)};
      font-family: ${props => props.theme.fonts.secondary};
      font-weight: 400;
    }
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(20)};
    width: ${mobileVw(187)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${props => (props.hasIcons ? desktopVw(55) : desktopVw(135))};
    p {
      b {
        font-size: ${desktopVw(48)};
        line-height: ${desktopVw(48)};
        font-family: ${props => props.theme.fonts.secondary};
        font-weight: 400;
      }
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(20)};
      width: ${desktopVw(390)};
      &:first-of-type {
        padding-bottom: ${props =>
          props.hasIcons ? desktopVw(33) : desktopVw(24)};
      }
    }
  }
`

const Card = styled.div`
  position: relative;
`

const ImageRowCards = ({ backgroundColor, cards, metaTags }) => {
  const { devices } = useDetectDevices()
  if (metaTags) {
    if (metaTags.includes('SHOW_ONLY_ON_DESKTOP') && !devices.desktop)
      return null
    if (metaTags.includes('SHOW_ONLY_ON_MOBILE') && !devices.mobile) return null
  }

  return (
    <StyledWrapper bgColor={backgroundColor?.hexcode}>
      {cards.map((card, index) => (
        <Card>
          <StyledScaledImage
            even={index % 2 === 0}
            singleImage={false}
            fluid={card?.imageDesktop?.fluid}
          />
          <TextCard hasIcons={card?.image}>
            {documentToReactComponents(card?.text?.json)}
            {card?.image && <SVGDesktop src={card?.image?.file?.url} />}
          </TextCard>
        </Card>
      ))}
    </StyledWrapper>
  )
}

export default ImageRowCards
